/* Breakpoint Definitions */
.gallery__container {
  grid-area: gallery; }
  .gallery__container picture img {
    width: 100%;
    height: auto;
    object-fit: cover; }

.gallery__row {
  display: grid;
  gap: 16px;
  margin-bottom: 8px; }
  @media all and (min-width: 960px) {
    .gallery__row {
      grid-template-columns: repeat(20, 1fr); } }

.gallery__link {
  display: none; }

/* Adds bottom margin to gallery__row to account for link and label */
.gallery__with-link-labels .gallery__link {
  display: flex;
  align-content: center;
  justify-content: center;
  grid-area: 2 / 1 / 2 / 1; }

.gallery__with-link-labels .gallery__link > a {
  border: none;
  background: #ffffff;
  padding: 4px;
  font-size: 16px;
  line-height: 28px;
  color: #444;
  font-family: "brandon-grotesque", "sans-serif";
  font-weight: 200;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 2.16px; }
  @media all and (min-width: 960px) {
    .gallery__with-link-labels .gallery__link > a {
      font-size: 18px;
      line-height: 32px; } }

.gallery__with-link-labels .gallery__row {
  margin-bottom: 24px; }
  @media all and (min-width: 960px) {
    .gallery__with-link-labels .gallery__row {
      margin-bottom: 36px; } }

/* Addresses images to locations in the grid row */
.image-0 {
  grid-area: one; }

.image-1 {
  grid-area: two; }

.image-2 {
  grid-area: three; }

.image-3 {
  grid-area: four; }

